const getGenerateColorsWorker = () => new Worker(new URL("./generate-colors.worker", import.meta.url), {
    type: "module",
});
export const createGenerateColors = () => {
    let prevWorker;
    return (hue, width, height) => {
        const worker = getGenerateColorsWorker();
        if (prevWorker) {
            prevWorker.terminate();
        }
        prevWorker = worker;
        return new Promise((res) => {
            worker.onmessage = (event) => {
                res(event.data.slice());
            };
            worker.postMessage([hue, width, height]);
        });
    };
};
